import { Component, ElementRef, OnInit, ViewChild, NgZone, AfterViewInit } from '@angular/core';
import { Camera, CameraResultType, CameraSource } from '@capacitor/camera';
import { NavController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { environment } from 'src/environments/environment';
import { ClaimFormService } from 'src/services/claim-form/claim-form.service';
import { ClaimReviewFormService } from 'src/services/claim-review-form/claim-review-form.service';
import { TranslateConfigService } from 'src/services/translate-config/translate-config.service';
import { UrlService } from 'src/services/url/url.service';

@Component({
  selector: 'app-upload-document',
  templateUrl: './upload-document.page.html',
  styleUrls: ['./upload-document.page.scss'],
})
export class UploadDocumentPage implements OnInit, AfterViewInit {
  @ViewChild('videoElement', { static: true }) videoElement!: ElementRef<HTMLVideoElement>;
  @ViewChild('canvas', { static: true }) canvas!: ElementRef<HTMLCanvasElement>;
  readonly screenWidth = window.innerWidth;
	readonly screenHeight = window.innerHeight;
  readonly assetUrl = environment.assetURL;

  capturedImage: string | null = null;
  stream: MediaStream | null = null;
  formData: any;
  prevUrl: any;
  previousUrl: any;

  goBack() {
    this.stopCamera();

    if (this.prevUrl != null) {
			this.navCtrl.navigateBack(this.prevUrl, {
				replaceUrl: true,
				animated: true,
			});
			this.urlService.removePreviousUrl();
			return;
		}
    
    this.navCtrl.navigateBack("/claim-form-doc", {
			replaceUrl: true,
			animated: true,
		});
  }

  async requestCameraPermission() {
    try {
      const permissionStatus = await navigator.permissions.query({ name: 'camera' as PermissionName });
      console.log('Permission status:', permissionStatus.state);

      if (permissionStatus.state === 'denied') {
        alert('Camera permission denied. Please enable it in browser settings.');
        return;
      }

      await this.startWebCamera();
    } catch (err) {
      console.error('Permission check error:', err);
      await this.startWebCamera();
    }
  }

  async startWebCamera() {
    if (!this.videoElement?.nativeElement) {
      console.warn('Video element not available');
      return;
    }

    // Clean up existing stream
    if (this.stream) {
      this.stream.getTracks().forEach(track => track.stop());
      this.stream = null;
      this.videoElement.nativeElement.srcObject = null;
    }

    const constraints: MediaStreamConstraints = {
      video: {
        facingMode: { ideal: 'environment' } // Prefer rear camera
      },
      audio: false
    };

    try {
      this.stream = await navigator.mediaDevices.getUserMedia(constraints);
      this.videoElement.nativeElement.srcObject = this.stream;
      console.log('Camera started successfully');
    } catch (err: any) {
      console.error('Camera error:', err);

      if (err.name === 'NotAllowedError') {
        alert('Camera permission was denied. Please allow camera access.');
      } else if (err.name === 'NotFoundError') {
        alert('No camera found on this device.');
      } else {
        alert('Failed to start camera: ' + (err.message || err.name));
      }
    }
  }

  captureWeb() {
    const video = this.videoElement.nativeElement;
    const canvas = this.canvas.nativeElement;
    const ctx = canvas.getContext('2d');
    if (!ctx || !video.videoWidth || !video.videoHeight) return;

    const container = video.parentElement;
    if (!container) return;

    const containerWidth = container.clientWidth;
    const containerHeight = container.clientHeight;

    canvas.width = containerWidth;
    canvas.height = containerHeight;

    const videoAspectRatio = video.videoWidth / video.videoHeight;
    const containerAspectRatio = containerWidth / containerHeight;

    let srcX = 0, srcY = 0, srcW = video.videoWidth, srcH = video.videoHeight;
    let dstX = 0, dstY = 0, dstW = containerWidth, dstH = containerHeight;

    if (videoAspectRatio > containerAspectRatio) {
      srcW = video.videoHeight * containerAspectRatio;
      srcX = (video.videoWidth - srcW) / 2;
    } else {
      srcH = video.videoWidth / containerAspectRatio;
      srcY = (video.videoHeight - srcH) / 2;
    }

    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(
      video,
      srcX, srcY, srcW, srcH,
      dstX, dstY, dstW, dstH
    );

    this.capturedImage = canvas.toDataURL('image/png');
  }

  stopWebCamera() {
    if (this.stream) {
      this.stream.getTracks().forEach(track => track.stop());
      this.stream = null;
    }
  }

  capture() {
    this.captureWeb();

    this.stopCamera();
  }

  retake() {
    this.capturedImage = null;
    setTimeout(async () => {
      await this.startWebCamera();
    }, 0);
  }

  stopCamera() {
    this.stopWebCamera();
  }

  saveDoc() {
    console.log(JSON.stringify(this.formData));

    if (this.prevUrl.includes("history")) {
      this.claimReviewFormService.updateFormData({
        documents: [
          ...this.formData?.documents,
          {
            id: Date.now().toString(36) + Math.random().toString(36).slice(2, 8),
            filename: Date.now().toString() + "_doc.png",
            image: this.capturedImage
          }
        ]
      })
      return;
    }

    this.claimFormService.updateFormData({
      documents: [
        ...this.formData?.documents,
        {
          id: Date.now().toString(36) + Math.random().toString(36).slice(2, 8),
          filename: Date.now().toString() + "_doc.png",
          image: this.capturedImage
        }
      ]
    })
  }

  constructor(
    private navCtrl:NavController,
    public translate: TranslateService,
    public translateConfigService: TranslateConfigService,
    private claimFormService: ClaimFormService,
    private claimReviewFormService: ClaimReviewFormService,
    public urlService: UrlService
  ) {
    this.translateConfigService.getDefaultLanguage();
  }

  async ngOnInit() {
    this.urlService.previousUrl$.subscribe((previousUrl: string[]) => {
			this.previousUrl = previousUrl;
		});
		this.prevUrl = this.previousUrl[this.previousUrl.length - 1];
  }

  async ngAfterViewInit() {
    setTimeout(async () => {
      await this.startWebCamera();
    }, 0);
  }

  ionViewWillEnter() {
    if (this.prevUrl.includes("history")) {
      this.formData = this.claimReviewFormService.getCurrentValue();
      console.log(JSON.stringify(this.formData));
    } else {
      this.formData = this.claimFormService.getCurrentValue();
      console.log(JSON.stringify(this.formData));
    }
  }

  ngOnDestroy() {
    this.stopCamera();
  }
}